function createReproductionLink(link: string) {
  if (!link) {
    return;
  }

  if (link.indexOf('codesandbox.io') >= 0) {
    return `[![Edit on CodeSandbox](https://codesandbox.io/static/img/play-codesandbox.svg)](${link})`;
  }

  return `[${link}](${link})`;
}
